.books-container {
    border-bottom: 1.5px solid #edf3e7;
    padding: 1rem;
}

.category {
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 0.75rem;
    margin-left: 0.5rem;
}

.books {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.book-image {
    cursor: pointer;
}

.header {
  width: 100%;
  height: 3.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading {
  font-size: 2rem;
  text-transform: uppercase;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.col { 
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.loading {
  min-height: calc(100vh - 4rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
  font-weight: bold;
  font-size: 3rem;
}

.btn {
  color: #212529;
  text-align: center;
  background-color: transparent;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  display: block;
  margin: 1rem 0;
  cursor: pointer;
}

.shadow {
  box-shadow: 0 3px 5px -1px rgba(0,0,0,.2), 0 5px 8px 0 rgba(0,0,0,.14), 0 1px 14px 0 rgba(0,0,0,.12);
}

.align-end {
  display: flex;
  justify-content: flex-end;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.image-container {
    display: flex;
    justify-content: center;
}

.book-title {
    font-weight: 400;
    font-style: normal;
    font-size: 2.08rem;
    line-height: 1.25;
}

.book-author {
    color: #347d56;
    font-weight: bold;
}

.book-description {
    margin: 3rem 0;
    font-size: 1.15rem;
    font-weight: 600;
    color: grey;

}
.book-purchase {
    font-size: 1.25rem;
    font-weight: bold;
    color: gray;
    margin-bottom: 1rem;
}

.book-purchase-link {
    margin-right: 1rem;
}

.btn-primary {
    background-color: #edf3e7;
    border-color: #edf3e7;
    text-transform: uppercase;
    font-weight: bold;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
    font-family: "cheltenham-normal-400",georgia,"times new roman",times,serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p, h1, h2, h3, h4, h5 {
  padding: 0;
  margin: 0;
}
